      .select-wrap{
          width: 100%;
          max-width: 200px;
          border: 1px solid #ddd;
          background-color: #FFFFFF;
          padding: 3px 5px;
          position: relative !important;
          cursor: pointer;
          transition: .5s;
          border-radius: 5px;
          color: initial !important;
          font-size: 14px;
          align-self: center !important;
          max-height: 45px;
      }
      .border-b{
          background: transparent;
          border-top: none;
          border-right: none;
          border-left: none;
          border-radius: 0px;
          border-bottom: 1px solid #ddd;
      }
      .border-b input[type='text']{
          background-color: transparent;
          padding: 0px !important;
          color: #aaa;
          font-size: 14px;
          border: none !important;
      }
      .border-b:hover{
          border-top: none !important;
          border-right: none !important;
          border-left: none !important;
          border-bottom: 1px solid ;
      }
      .select-wrap:hover{
          border: 1px solid #007aff;
          transition: .5s
      }
      .select-wrap .text .fa{
          right: 5px;
          position: absolute;
          top: 35%;
          font-size: 16px;
          color: #aaa;
          transition: .5s;
      }
      .select-wrap input{
          cursor: pointer;
          text-transform: capitalize;
          border:none !important;
      }
  
      .select-wrap:hover > .option{
          visibility: visible;
          opacity: 1;
          top: 108%;
          transition: .3s;
          z-index: 999;
      }
      .select-wrap:hover .text .fa{
          -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
          transition: .5s;
      }
      .select-wrap .option{
          position: absolute;
          width: 100%;
          box-sizing: border-box;
          left: 0px;
          box-shadow: 0px 5px 30px rgba(0,0,0,.1);
          top: 60%;
          opacity: 0;
          visibility: hidden;
          transition: .1s ease-in;
          max-height: 250px;
          overflow-x: hidden;
          overflow-y: scroll;
          background-color: #fff;
      }
      .hide{
          top: 60% !important;
          opacity: 0 !important;
          visibility: hidden !important;
      }
      .select-wrap .option li{
          display: block !important;
          position: relative !important;
          background: #fff;
          color: #555;
          width: 100%;
          box-sizing: border-box;
          overflow: hidden;
          transition: .5s;
          left: -90px;
          padding: 10px 15px;
          border-bottom: 1px solid #EEEEEE;
          background: #FFFFFF;
  
      }
      .select-wrap .option li .fa{
          color: #aaa;
          padding-right: 8px;
      }
      .select-wrap .option li:first-child{
          transition: .4s;
      }
      .select-wrap .option li:nth-child(even){
          transition: .6s;
      }
      .select-wrap .option li:nth-child(odd){
          transition: .9s;
      }
      .select-wrap .option li:hover{
          opacity: .8;
      }
      .select-wrap:hover .option li{
          left: 0px;
      }
      .select-wrap:hover .option li:first-child{
          transition: .4s;
      }
      .select-wrap:hover .option li:nth-child(2){
          transition: .6s;
      }
      .select-wrap:hover .option li:nth-child(3){
          transition: .9s;
      }
      .select-wrap .option .selected{
          background: #007aff;
          color: #fff;
          box-shadow: inset 0px 0px 30px rgba(0,0,0,.3)
      }
      .select-wrap .option li input{
          width: 100%;
          height: 100%;
          top:0px;
          position: absolute !important;
          opacity: 0;
          cursor: pointer !important;
      }
  
